.main__form {
  margin-top: 25px;
  gap: 30px;
  text-align: center;
}
.container {
  display: flex;
  justify-content: space-around;
}

.title {
  text-transform: capitalize;
  color: rgb(19, 88, 19);
  margin-top: 20px;
  margin-bottom: 20px;
}
.result {
  display: flex;
  flex-direction: column;
}
.article {
  box-shadow: 0px 0px 5px 1px grey;
  border: solid rgb(19, 88, 19);
  padding: 10px;
  width: 140px;
  height: 220;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 7px;
  cursor: pointer;
}
.article.selected {
  background-color: rgb(113, 175, 113);
}
.img {
  height: 140px;
  width: 120px;
}
